import React from "react";
import wrapper from "../../../commons/WebComponentWrapper";
import AppBar from '@mui/material/AppBar';

const dispatchedEvents = [];

const SrsMuiAppBar = function (props, ref) {
  let componentProps = {...props, theme: props.muiTheme};
  return (
    <AppBar {...componentProps}>
      <slot />
    </AppBar>
  );
}

const styles = `
  /* Custom styles for the AppBar component */
`;

const props = [
  {
    name: "color",
    defaultValue: "primary",
    type: "string",
  },
  {
    name: "position",
    defaultValue: "fixed",
    type: "string",
  },
  {
    name: "elevation",
    defaultValue: 4,
    type: "number",
  },
  {
    name: "disableGutters",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "variant",
    defaultValue: "elevation",
    type: "string",
  },
  {
    name: "className",
    defaultValue: "",
    type: "string",
  },
  {
    name: "sx",
    defaultValue: {},
    type: "object",
  },
  // Add other props specific to AppBar if needed
];

const slots = [
  {
    name: "default",
    allowed: [],
  },
];

const WrappedComponent = wrapper(React.forwardRef(SrsMuiAppBar), props, "mui");
export { WrappedComponent, styles, props, dispatchedEvents, slots };
